.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  /* position: relative; */
  width: 200px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  max-width: 350px;
  max-height: 330px;
  width: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.images {
  width: 100%;
  height: 100%;
}



